@mixin fontStyleNormal($lineHeight: 15px) {
  font-weight: 500;
  font-size: 12px;
  line-height: $lineHeight;
}

@mixin fontSmall($lineHeight: 13px) {
  font-weight: 400;
  font-size: 11px;
  line-height: $lineHeight;
}

@mixin spaceBetween {
  display: flex;
  justify-content: space-between;
}
